<style lang="less" scoped>
  .container {
    background-color: #eee;
  }
  .area {
    padding: 30px 20px;
    &>div {
      padding-left: 20px;
    }
    &>div {
      background-color: #fff;
      box-sizing: border-box;
      margin-bottom: 20px;
      display: flex;
      span {
        font-size: 1rem;
        display: inline-block;
        height: 70px;
        line-height: 70px;
        flex: 3;
        text-indent: 0.5em;
        font-size: 25px;
      }
      input {
        border: none;
        outline: none;
        height: 70px;
        text-indent: 1em;
        flex: 7;
        font-size: 25px;
      }
    }
  }
  .submitBtn {
    width: 100%;
    text-align: center;
    &>button {
      width: 500px;
      height: 80px;
      background: #6dce67;
      text-align: center;
      line-height: 80px;
      color: #fff;
      font-size: 30px;
      border: none;
      outline: none;
      border-radius: 20px;
    }
  }
</style>

<template>
  <div class="container">
    <headerNavBack :centerTitle="centerTitle"></headerNavBack>
    <div class="area">
      <div>
        <span>验证密码</span>
        <input placeholder="请输入当前密码" type="password" ref="initPasswd" v-model="oldPasswd">
      </div>
      <div>
        <span>设置密码</span>
        <input placeholder="请输入新密码" type="password" ref="setPasswd" v-model="newPasswd">
      </div>
      <div>
        <span>确认密码</span>
        <input placeholder="请再次输入密码" type="password" ref="SurePasswd" v-model="aginPasswd">
      </div>
    </div>
    <div class="submitBtn">
      <button @click="submitBtn">完成</button>
    </div>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  export default {
    components: {
      headerNavBack,
    },
    data() {
      return {
        user: {},
        oldPasswd: '',
        newPasswd: '',
        aginPasswd: '',
        captcha: '',
        centerTitle: '修改密码',
      }
    },
    methods: {
      back() {
        this.$router.go(-1)
      },
      submitBtn() {
        if (this.oldPasswd.length == 0) {
          this.$Toast({
            message: '请输入原密码',
            position: 'middle',
            duration: 500
          })
          return
        }
        if (this.newPasswd.length == 0) {
          this.$Toast({
            message: '请输入新密码',
            position: 'middle',
            duration: 500
          })
          return
        }
        if (this.aginPasswd.length == 0) {
          this.$Toast({
            message: '请再次输入密码',
            position: 'middle',
            duration: 500
          })
          return
        }
        if (this.newPasswd !== this.aginPasswd) {
          this.$Toast({
            message: '两次密码不一致',
            position: 'middle',
            duration: 500
          })
          return
        }
        this.$store.dispatch('MODIFIED_USER_PWD', {
          primary_password: this.oldPasswd,
          password: this.aginPasswd
        }).then(data => {
          if(data.data.status === 'success'){
            this.$Toast({
              message: '修改密码成功',
              position: 'middle',
              duration: 500
            })
            this.oldPasswd = ''
            this.newPasswd = ''
            this.aginPasswd = ''
          }else{
            this.$Toast({
              message: data.data.result,
              position: 'middle',
              duration: 500
            })
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
    mounted() {
      
    }
  }
</script>



